import React from "react";

const ProjectCardCustom = ({ value }: any) => {

    const {
        name,
        description,
        svn_url,
        homepage,
        languages,
        last_update,
        custom_link,
        custom_link_text
    } = value;

    return (
        <div className="col-md-6">
            <div className="card shadow p-3 mb-5 bg-white rounded">
                <div className="card-body">
                    <div className="card-title h3">{name}</div>
                    <div className="card-text">{description}</div>
                    <CardButtons svn_url={svn_url} homepage={homepage} custom_link={custom_link} custom_link_text={custom_link_text} />
                    <hr />
                    <Language languages={languages} />
                    <CardFooter last_update={last_update} />
                </div>
            </div>
        </div>
    )
}

const CardButtons = ({ svn_url, homepage, custom_link, custom_link_text }: any) => {

    return (
        <>
            {homepage !== "" ? (
                <a href={homepage} className="btn btn-outline-success" target=" _blank"    >
                    <i className="" /> Run
                </a>)
                :
                (null)
            }
            {svn_url !== "" ? (
                <a href={svn_url} className="btn btn-outline-success" target=" _blank"    >
                    <i className="" /> Run
                </a>)
                :
                (null)
            }
            {custom_link !== "" ? (
                <a href={custom_link} className="btn btn-outline-success" target=" _blank"    >
                    <i className="" /> {custom_link_text}
                </a>)
                :
                (null)
            }

        </>
    )

    // if (homepage === "") {

    //     // Create repo button
    //     return (
    //         <>
    //             <a href={svn_url} target=" _blank" className="btn btn-outline-secondary">
    //                 <i className="" /> View
    //             </a>
    //         </>
    //     );
    // }
    // else {
    //     // Create run and repo buttons
    //     return (
    //         <>
    //             <a href={homepage} className="btn btn-outline-success" target=" _blank"    >
    //                 <i className="" /> Run
    //             </a>
    //             <a href={svn_url} className="btn btn-outline-secondary" target=" _blank">
    //                 <i className="" /> Repo
    //             </a>
    //         </>
    //     );
    // }
};

const Language = ({ languages }: any) => {

    const array = [];
    for (let x in languages) {
        array.push(languages[x])
    }

    return (
        <div className="pb-3">
            Languages:{" "}
            {array.map((language) =>
            (
                <div className="badge badge-light card-link" key={language} >{language}</div>
            )
            )}
        </div>
    );
};

const CardFooter = ({ last_update }: any) => {

    return (
        <p className="card-text">
            <small className="text-muted">Updated {last_update}</small>
        </p>
    )
};

export default ProjectCardCustom;