import React, { Component } from 'react'

export default class About extends Component {
  render() {
    return (
        <section className="colorlib-about" data-section="about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-12">
                <div className="row row-bottom-padded-sm">
                    <div className="about-desc">
                      <h2 className="colorlib-heading">About</h2>
                      <span className="heading-meta">Software Developer</span>
                        <p className="global-paragraph">
                        My name is John Kotronakis. I am a Business Administration graduate hailing from the Island of Crete in Greece.
                        Throughout life, my passion revolved around computers. 
                        The past few years I've been acquiring skills and experience in the fields of software engineering and computer science with the goal of becoming
                        an established Software Developer.
                        </p>           
                        <p className="global-paragraph">
                          I spend most of my free time either playing or modding video games to my liking. My favourite video game is Fallout New Vegas.
                        </p>     
                    </div>
                </div>
            </div>
            </div>
        </div>
        </section>
    )
  }
}
