import React from 'react'
import { useForm, ValidationError } from '@formspree/react';


const ContactForm = () => {
  const [state, handleSubmit] = useForm("maylkbdv");

  if (state.succeeded) {
    return <p>Message sent!</p>;
  }
  return (
    <form onSubmit={handleSubmit}>

      <div className="form-group">
        <label htmlFor="name">
          Name
        </label>
        <input
          className="form-control"
          id="name"
          type="name"
          name="name"
          placeholder="Your Name"
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="email">
          Email Address
        </label>
        <input
          className="form-control"
          id="email"
          type="email"
          name="email"
          placeholder="Your Email address"
          required
        />
        <ValidationError
          prefix="Email"
          field="email"
          errors={state.errors}
        />
      </div>
      <div className="form-group">
        <label htmlFor="message">
          Message
        </label>
        <textarea
          className="form-control"
          id="message"
          name="message"
          placeholder="Your Message"
        />
        <ValidationError
          prefix="Message"
          field="message"
          errors={state.errors}
        />
      </div>
      <div className="form-group">
        <button type="submit" disabled={state.submitting} className="btn btn-primary btn-send-message">
          Submit
        </button>
      </div>
    </form>
  );
}

const Contact = () => {
  return (
    <section className="colorlib-contact" data-section="contact">
      <div className="colorlib-narrow-content">
        <div className="row row-bottom-padded-sm">
          <h2 className="colorlib-heading">Contact</h2>
          <div className="col-md-7 col-md-push-1">
            <ContactForm />
          </div>
        </div>
      </div>
    </section>
  );
}
export default Contact;